/* eslint-disable */
<template>
  <el-form :model="registerForm" status-icon :rules="rules" ref="registerForm" label-position="left" label-width="0px" class="register-container">
    <h3 class="register_title">用户注册</h3>
    <el-form-item prop="username">
      <el-input type="text" v-model="registerForm.username" auto-complete="off" placeholder="身份证号"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="registerForm.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item prop="checkPassword">
      <el-input type="password" v-model="registerForm.checkPassword" auto-complete="off" placeholder="确认密码"></el-input>
    </el-form-item>
    <el-form-item>
      <a href="/login" class="btn" role="button" id="return_btn">返回</a>
      <el-button type="primary" style="width: 30%;background: #505458;border: none;margin-left: 95px" @click="register('registerForm')">注册</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
/* eslint-disable */
export default {
  name: 'Register',
  data() {
    var checkUsername = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('身份证号不能为空'));
      }
      setTimeout(() => {
        if (!/^\d+$/.test(value)) {
          callback(new Error('身份证号格式不正确1'));
        } else {
          if (value.length != 18) {
            callback(new Error('身份证号格式不正确2'));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.registerForm.checkPassword !== '') {
          this.$refs.registerForm.validateField('checkPassword');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.registerForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        username: '',
        password: '',
        checkPassword: '',
      },
      rules: {
        username: [
          { validator: checkUsername, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPassword: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    register(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                this.$axios
                  .post('/register', {
                    username: this.registerForm.username,
                    password: this.registerForm.password
                  })
                  .then(successResponse => {
                    if (successResponse.data.code === 200) {
                      this.$alert('注册成功', '提示', {
                                      confirmButtonText: '确定'
                                    })
                      this.$router.replace({path: '/index'})
                    }
                    else {
                                  this.$alert(successResponse.data.message, '提示', {
                                    confirmButtonText: '确定'
                                  })
                                }
                  })
                  .catch(failResponse => {
                  })
              } else {
                console.log('error register!!');
                return false;
              }
            })
          }
  }
}
</script>
<style>
  .register-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 500px;
    padding: 35px 35px 15px 35px;
    background: aliceblue;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .register_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
</style>



