<template>
  <div style="margin-top: 30px; margin-bottom: 30px;display: flex;justify-content: center;align-items: center">
    <el-input
      @keyup.enter.native="searchClick"
      placeholder="搜索航班号.."
      prefix-icon="el-icon-search"
      size="small"
      style="width: 200px;margin-right: 10px"
      v-model="keywords1"
      clearable>
    </el-input>
    <el-input
      @keyup.enter.native="searchClick"
      placeholder="搜索出发地.."
      prefix-icon="el-icon-search"
      size="small"
      style="width: 200px;margin-right: 10px"
      v-model="keywords2"
      clearable>
    </el-input>
    <el-input
      @keyup.enter.native="searchClick"
      placeholder="搜索目的地.."
      prefix-icon="el-icon-search"
      size="small"
      style="width: 200px;margin-right: 10px"
      v-model="keywords3"
      clearable>
    </el-input>
    <el-date-picker
      v-model="date"
      align="right"
      type="date"
      value-format="yyyy-MM-dd"
      placeholder="选择日期..">
    </el-date-picker>
    <el-checkbox v-model="checked">显示已满航班</el-checkbox>
    <el-button size="small" type="primary" icon="el-icon-search" @click="searchClick">搜索</el-button>
  </div>
</template>

<script>
  export default {
    name: 'TicketSearchBar',
    data () {
      return {
        keywords1: '',
        keywords2: '',
        keywords3: '',
        date: '',
        checked: false
      }
    },
    methods: {
      searchClick () {
        this.$emit('onSearch')
      }
    }
  }
</script>

<style scoped>

</style>




