<template>
  <div class="container-fluid">
    <el-row>
      <ticket-search-bar @onSearch="searchResult" ref="TicketSearchBar"></ticket-search-bar>
      <order-add-form @onSubmit="load()" ref="OrderAddForm"></order-add-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
         highlight-current-row
        @current-change="handleRowChange"
        @sort-change="sortChange"
        @filter-change="filterChange"
        stripe
        style="width: 100%">
        <el-table-column
          prop="flightNum"
          label="航班编号"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.departure"
          label="出发地"
          sortable="custom"
          width="120">
        </el-table-column>
        <el-table-column
          prop="airline.destination"
          label="到达地"
          sortable="custom"
          width="120">
        </el-table-column>
        <el-table-column
          prop="flightDate"
          label="日期"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.checkTime"
          label="登机时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.takeOffTime"
          label="起飞时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.arriveTime"
          label="到达时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.xprice"
          label="头等舱票价"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="airline.yprice"
          label="经济舱票价"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="seatState"
          label="已购座数/总座数"
          :formatter="formatSeat"
          width="150">
        </el-table-column>
        <el-table-column
          column-key="state"
          prop="state"
          label="状态"
          width="150"
          :formatter="formatState"
          :filters="[{ text: '待飞', value: 300 }, { text: '正在检票', value: 301 },{ text: '正在途中', value: 302 },{ text: '延误', value: 303 },{ text: '取消', value: 304 }]"
          filter-placement="bottom-end">
        </el-table-column>
        <el-table-column
          label="操作"
          width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="order(scope.row)">订票</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import OrderAddForm from './OrderAddForm.vue'
  import TicketSearchBar from './TicketSearchBar.vue'
  export default {
    name: 'Ticket',
    components: {OrderAddForm,TicketSearchBar},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          state: '',
          checked: false,
          keywords1: '',
          keywords2: '',
          keywords3: '',
          date: ''
        },
        currentRow: null
      }
    },
    mounted: function () {
      this.load()
    },
    methods: {
      load () {
        var _this = this
        if(this.filterParams.state === '' && !this.filterParams.checked && this.filterParams.keywords1 === '' && this.filterParams.keywords2 === '' && this.filterParams.keywords3 === '' && this.filterParams.date === '') {
          this.$axios.get('/flights').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          this.$axios.get('/search/ticket',{params:{stateCodes:this.filterParams.state,checked:this.filterParams.checked,keywords1:this.filterParams.keywords1,keywords2:this.filterParams.keywords2,keywords3:this.filterParams.keywords3,date:this.filterParams.date}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      handelRowChange(currentRow, oldCurrentRow){
        if(currentRow === oldCurrentRow)
          this.order(currentRow);
      },
      filterChange(filter) {
        this.filterParams.state = filter.state.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          a = a[attr];
          b = b[attr];
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatSeat(row) {
        var s = row.xorderCount + row.yorderCount;
        var n = row.airline.plane.xcapacity + row.airline.plane.ycapacity;
        return s + "/" + n;
      },
      formatState(row) {
        return row.state === 300 ? "待飞" : row.state === 301 ? "正在检票" : row.state === 302 ? "正在途中" : row.state === 303 ? "延误" : "取消";
      },
      searchResult () {
        var _this = this
       this.filterParams.keywords1 = this.$refs.TicketSearchBar.keywords1;
       this.filterParams.keywords2 = this.$refs.TicketSearchBar.keywords2;
       this.filterParams.keywords3 = this.$refs.TicketSearchBar.keywords3;
       this.filterParams.date = this.$refs.TicketSearchBar.date;
       this.filterParams.checked = this.$refs.TicketSearchBar.checked;
       this.currentPage = 1;
       this.load();
      },
      order (item) {
        if(item.airline.plane.xcapacity === item.xorderCount && item.airline.plane.ycapacity === item.yorderCount){
          this.$message({message: '该航班已满', type: 'error'});
          return;
        }
        if(item.state !== 300 && item.state !== 303){
          this.$message({message: '已开始检票或起飞后无法购票', type: 'error'});
          return;
        }
        this.$refs.OrderAddForm.dialogFormVisible = true;
        this.$refs.OrderAddForm.add = true;
        this.$refs.OrderAddForm.form = {
          flightId: item.flightId,
          flightNum: item.flightNum,
          flightDate: item.flightDate,
          departure: item.airline.departure,
          destination: item.airline.destination,
          checkTime: item.airline.checkTime,
          takeOffTime: item.airline.takeOffTime,
          arriveTime: item.airline.arriveTime,
          xprice: item.airline.xprice,
          yprice: item.airline.yprice,
          xLimit: item.airline.plane.xcapacity - item.xorderCount,
          yLimit: item.airline.plane.ycapacity - item.yorderCount,
          xNum: 0,
          yNum: 0,
          sum: 0
        }
      }
    }
  }
</script>

<style scoped>

</style>



