<template>
  <div>
    <el-menu
      :default-active="'/index'"
      router
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      style="min-width: 1300px">
      <template v-for="(item,i) in adminMenus">
        <el-menu-item :index="item.path">
          {{ item.nameZh }}
        </el-menu-item>
      </template>
      <el-button type="text" v-on:click="logout" style="color:aliceblue; height: 60px; float: right; text-align: center; margin: 0px 20px">登出</el-button>
    </el-menu>
  </div>
</template>

<script>
  export default {
    name: 'NavMenu',
    computed: {
      adminMenus () {
        return this.$store.state.adminMenus
      }
    },
    methods: {
      logout () {
          var _this = this
          this.$axios.get('/logout').then(resp => {
            if (resp.data.code === 200) {
              // 前后端状态保持一致
              _this.$store.commit('logout')
              _this.$router.replace('/login')
            }
          })
        }
    }
  }
</script>

<style scoped>
  a{
    text-decoration: none;
  }

  span {
    pointer-events: none;
  }
</style>




