<template>
  <div class="container">
    <el-row>
      <order-add-form @onSubmit="load()" ref="OrderAddForm"></order-add-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        border
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="出发地">
                <span>{{ props.row.flight.airline.departure }}</span>
              </el-form-item>
              <el-form-item label="目的地">
                <span>{{ props.row.flight.airline.destination }}</span>
              </el-form-item>
              <el-form-item label="日期">
                <span>{{ props.row.flight.flightDate }}</span>
              </el-form-item>
              <el-form-item label="登机时间">
                <span>{{ props.row.flight.airline.checkTime }}</span>
              </el-form-item>
              <el-form-item label="起飞时间">
                <span>{{ props.row.flight.airline.takeOffTime }}</span>
              </el-form-item>
              <el-form-item label="到达时间">
                <span>{{ props.row.flight.airline.arriveTime }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="单号"
          sortable="custom"
          width="180">
        </el-table-column>
        <el-table-column
          prop="flight.flightNum"
          label="航班号"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="time"
          label="下单时间"
          sortable="custom"
          width="200"
          :formatter="formatTime">
        </el-table-column>
        <el-table-column
          column-key="state"
          prop="state"
          label="订单状态"
          width="120"
          :filters="[{ text: '已提交', value: 500 }, { text: '已完成', value: 501 },{ text: '已取消', value: 502 },{ text: '已过期', value: 503 }]"
          filter-placement="bottom-end"
          :formatter="formatState">
        </el-table-column>
        <el-table-column
          prop="xnum"
          sortable
          label="头等舱座(个)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="ynum"
          sortable
          label="经济舱座(个)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="totalPrice"
          sortable
          label="总价(元)"
          sortable="custom"
          width="150"
          :formatter="formatTotalPrice">
        </el-table-column>
        <el-table-column
          label="操作"
          width="150">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editOrder(scope.row)">修改</el-button>
            <el-button type="danger" size="small" @click="cancelOrder(scope.row.orderId)">取消</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import OrderAddForm from './OrderAddForm.vue'
  export default {
    name: 'MyOrder',
    components: {OrderAddForm},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          state: ''
        }
      }
    },
    mounted: function () {
      this.load()
    },
    methods: {
      load () {
        var _this = this
        if(this.filterParams.state === '') {
          this.$axios.get('/myOrders').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          this.$axios.get('/search/myOrders',{params:{stateCodes:this.filterParams.state}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      filterChange(filter) {
        this.filterParams.state = filter.state.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          if(attr === "flight.flightNum"){
            a = a.flight.flightNum;
            b = b.flight.flightNum;
          }
          else if(attr === "totalPrice") {
            a = a["totalXPrice"] + a["totalYPrice"];
            b = b["totalXPrice"] + b["totalYPrice"];
          }
          else{
            a = a[attr];
            b = b[attr];
          }
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatState(row) {
        return row.state === 500 ? "已提交" : row.state === 501 ? "已完成" : row.state === 502 ? "已取消" :  "已过期";
      },
      formatTotalPrice(row) {
        return row.totalXPrice + row.totalYPrice;
      },
      formatTime(row) {
        var t = row.time;
        return t.substring(0,10) + " " + t.substring(11,19);
      },
      cancelOrder(orderId) {
        this.$confirm('确认取消订单?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/cancel/orders', {orderId: orderId}).then(resp => {
              if (resp && resp.status === 200) {
                this.$message({message: '取消成功', type: 'success'})
                this.load()
              }
            })
          }
        ).catch(() => {
          this.$message({message: '晚于起飞前1小时 无法取消订单', type: 'error'})
        })
      },
      editOrder (item) {
        this.$refs.OrderAddForm.dialogFormVisible = true
        this.$refs.OrderAddForm.form = {
          flightId: item.flight.flightId,
          flightNum: item.flight.flightNum,
          flightDate: item.flight.flightDate,
          departure: item.flight.airline.departure,
          destination: item.flight.airline.destination,
          checkTime: item.flight.airline.checkTime,
          takeOffTime: item.flight.airline.takeOffTime,
          arriveTime: item.flight.airline.arriveTime,
          xprice: item.flight.airline.xprice,
          yprice: item.flight.airline.yprice,
          xLimit: item.flight.airline.plane.xcapacity - item.flight.xorderCount + item.xnum,
          yLimit: item.flight.airline.plane.ycapacity - item.flight.yorderCount + item.ynum,
          xNum: item.xnum,
          yNum: item.ynum,
          sum: item.flight.airline.xprice * item.xnum + item.flight.airline.yprice * item.ynum
        };
        this.$refs.OrderAddForm.orderId = item.orderId;
        this.$refs.OrderAddForm.account = item.account;
      }
    }
  }
</script>

<style>
  .demo-table-expand {
    margin-left: 80px;
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 40%;
  }
</style>



