<template>
  <div class="container-fluid">
    <el-row>
      <order-search-bar @onSearch="searchResult" ref="OrderSearchBar"></order-search-bar>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        border
        :summary-method="getSummaries"
        show-summary
        style="width: 100%">
        <el-table-column
          prop="orderId"
          label="单号"
          sortable="custom"
          width="180">
        </el-table-column>
        <el-table-column
          prop="account.username"
          label="用户名"
          sortable="custom"
          width="180">
        </el-table-column>
        <el-table-column
          prop="flight.flightNum"
          label="航班号"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="time"
          label="下单时间"
          sortable="custom"
          width="200"
          :formatter="formatTime">
        </el-table-column>
        <el-table-column
          column-key="state"
          prop="state"
          label="订单状态"
          width="120"
          :filters="[{ text: '已提交', value: 500 }, { text: '已完成', value: 501 },{ text: '已取消', value: 502 },{ text: '已过期', value: 503 }]"
          filter-placement="bottom-end"
          :formatter="formatState">
        </el-table-column>
        <el-table-column
          prop="xnum"
          sortable
          label="头等舱座(个)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="ynum"
          sortable
          label="经济舱座(个)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="TNum"
          sortable
          label="总座(个)"
          sortable="custom"
          width="100"
          :formatter="formatTNum">
        </el-table-column>
        <el-table-column
          prop="totalXPrice"
          sortable
          label="头等舱总价(元)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="totalYPrice"
          sortable
          label="经济舱总价(元)"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="totalPrice"
          sortable
          label="总价(元)"
          sortable="custom"
          width="150"
          :formatter="formatTotalPrice">
        </el-table-column>
        <el-table-column
          label="操作"
          width="100">
          <template slot-scope="scope">
            <el-button type="danger" size="small" @click="deleteOrder(scope.row.orderId)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import OrderSearchBar from './OrderSearchBar.vue'
  export default {
    name: 'OrderList',
    components: {OrderSearchBar},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          state: '',
          keywords: ''
        }
      }
    },
    mounted: function () {
      this.load()
    },
    methods: {
      load () {
        var _this = this
        if(this.filterParams.state === '' && this.filterParams.keywords === '') {
          this.$axios.get('/orders').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          this.$axios.get('/search/orders',{params:{stateCodes:this.filterParams.state,keywords:this.filterParams.keywords}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      filterChange(filter) {
        this.filterParams.state = filter.state.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          if(attr === "account.username"){
            a = a.account.username;
            b = b.account.username;
          }
          else if(attr === "flight.flightNum"){
            a = a.flight.flightNum;
            b = b.flight.flightNum;
          }
          else if(attr === "TNum") {
            a = a["xnum"] + a["ynum"];
            b = b["xnum"] + b["ynum"];
          }
          else if(attr === "totalPrice") {
            a = a["totalXPrice"] + a["totalYPrice"];
            b = b["totalXPrice"] + b["totalYPrice"];
          }
          else{
            a = a[attr];
            b = b[attr];
          }
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatState(row) {
        return row.state === 500 ? "已提交" : row.state === 501 ? "已完成" : row.state === 502 ? "已取消" :  "已过期";
      },
      formatTNum(row) {
        return row.xnum + row.ynum;
      },
      formatTotalPrice(row) {
        return row.totalXPrice + row.totalYPrice;
      },
      formatTime(row) {
        var t = row.time;
        return t.substring(0,10) + " " + t.substring(11,19);
      },
      searchResult () {
        var _this = this;
        this.filterParams.keywords = this.$refs.OrderSearchBar.keywords;
        this.currentPage = 1;
        this.load();
      },
      getSummaries(param) {
        const { columns, data } = param;
        var table = this.tableData;
        const sums = [];
        sums[0] = '合计';
        sums[1] = sums[2] = sums[3] = sums[4] = sums[11] = 'N/A';
        sums[5] = sums[6] = sums[8] = sums[9] = 0;
        for(var i=0; i<table.length; i++){
          sums[5] += table[i].xnum;
          sums[6] += table[i].ynum;
          sums[8] += table[i].totalXPrice;
          sums[9] += table[i].totalYPrice;
        }
        sums[7] = sums[5] + sums[6];
        sums[10] = sums[8] + sums[9];

        return sums;
      },
      deleteOrder(orderId) {
        this.$confirm('确认删除该订单?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delete/orders', {orderId: orderId}).then(resp => {
              if (resp && resp.status === 200) {
                this.$message({message: '删除成功', type: 'success'})
                this.load()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
        // alert(id)
      }
    }
  }
</script>

<style>
</style>



