<template>
  <div id="appIndex">
    <div id="systemcarousel" class="carousel slide carousel-fade" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#systemcarousel" data-slide-to="0" class="active"></li>
        <li data-target="#systemcarousel" data-slide-to="1"></li>
        <li data-target="#systemcarousel" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active">
          <img src="../../assets/images/index/carousel1.jpg" class="d-block w-100 navi_img" alt="1">
        </div>
        <div class="carousel-item">
          <img src="../../assets/images/index/carousel2.jpg" class="d-block w-100 navi_img" alt="2">
        </div>
        <div class="carousel-item">
          <img src="../../assets/images/index/carousel3.jpg" class="d-block w-100 navi_img" alt="3">
        </div>
      </div>
      <button class="carousel-control-prev" type="button" data-target="#systemcarousel" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </button>
      <button class="carousel-control-next" type="button" data-target="#systemcarousel" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </button>
    <div class="container mainpage">
      <h1 style="color: cyan;">欢迎来到AirT航空电子票务系统</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppIndex'
}
</script>

<style>
#appIndex{
  overflow: auto;
}
.navi_img{
  height: 400px;
}
.mainpage{
  margin-top: 20px;
  background-color: aliceblue;
  height: 2000px ;
}
.leftside {
  margin-left: 40px;
  margin-top: 20px;
  background-color: aliceblue;
  min-height: 1000px ;
  float: left;
  width: 15%;
}
.rightside {
  margin-right: 40px;
  margin-top: 20px;
  background-color: aliceblue;
  min-height: 1000px ;
  float: right;
  width: 15%;
}
</style>



