import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import AppIndex from '@/components/common/AppIndex'
import FlightList from '@/components/common/FlightList'
import AirlineList from '@/components/common/AirlineList'
import PlaneList from '@/components/common/PlaneList'
import AccountList from '@/components/common/AccountList'
import OrderList from '@/components/common/OrderList'
import Ticket from '@/components/common/Ticket'
import MyOrder from '@/components/common/MyOrder'
import Login from '@/components/Login'
import Register from '@/components/Register'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
     {
      path: '/home',
      name: 'Home',
      component: Home,
      // home页面并不需要被访问
      redirect: '/home/index',
      children: [
        {
          path: '/home/index',
          name: 'AppIndex',
          component: AppIndex,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/flights',
          name: 'FlightList',
          component: FlightList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/airlines',
          name: 'AirlineList',
          component: AirlineList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/planes',
          name: 'PlaneList',
          component: PlaneList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/accounts',
          name: 'AccountList',
          component: AccountList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/orders',
          name: 'OrderList',
          component: OrderList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/myOrder',
          name: 'myOrder',
          component: MyOrder,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/home/ticket',
          name: 'Ticket',
          component: Ticket,
          meta: {
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/index',
      redirect: '/home/index'
    },
    {
      path: '/flights',
      redirect: '/home/flights'
    },
    {
      path: '/airlines',
      redirect: '/home/airlines'
    },
    {
      path: '/planes',
      redirect: '/home/planes'
    },
    {
      path: '/accounts',
      redirect: '/home/accounts'
    },
    {
      path: '/orders',
      redirect: '/home/orders'
    },
    {
      path: '/myOrder',
      redirect: '/home/myOrder'
    },
    {
      path: '/ticket',
      redirect: '/home/ticket'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    }
  ]
})



