<template>
  <div class="container">
    <el-row>
      <plane-search-bar @onSearch="searchResult" ref="PlaneSearchBar"></plane-search-bar>
      <plane-edit-form @onSubmit="loadPlanes()" ref="PlaneEditForm"></plane-edit-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        stripe
        style="width: 100%">
        <el-table-column
          prop="planeNum"
          label="客机编号"
          sortable="custom"
          width="200">
        </el-table-column>
        <el-table-column
          prop="type"
          label="机型"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="xcapacity"
          label="头等舱座数"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="ycapacity"
          label="经济舱座数"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="capacity"
          label="总座数"
          sortable="custom"
          width="120"
          :formatter="formatCapacity">
        </el-table-column>
        <el-table-column
          column-key="state"
          prop="state"
          label="状态"
          width="100"
          :formatter="formatState"
          :filters="[{ text: '正常', value: 400 }, { text: '检修', value: 401 },{ text: '报废', value: 402 }]"
          filter-placement="bottom-end">
        </el-table-column>
        <el-table-column
          label="操作"
          width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editPlane(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="deletePlane(scope.row.planeId)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import PlaneEditForm from './PlaneEditForm.vue'
  import PlaneSearchBar from './PlaneSearchBar.vue'
  export default {
    name: 'PlaneList',
    components: {PlaneEditForm,PlaneSearchBar},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          state: '',
          keywords: ''
        }
      }
    },
    mounted: function () {
      this.loadPlanes()
    },
    methods: {
      loadPlanes () {
        var _this = this
        if(this.filterParams.state === '' && this.filterParams.keywords === '') {
          this.$axios.get('/planes').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          //this.$axios.get('/search/planes?stateCodes=' + this.filterParams.state + '?keywords=' + this.filterParams.keywords,{}).then(resp => {
          this.$axios.get('/search/planes',{params:{stateCodes:this.filterParams.state,keywords:this.filterParams.keywords}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页 ${this.pageSize} 条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页: ${this.currentPage}');
      },
      filterChange(filter) {
        this.filterParams.state = filter.state.join(',');
        this.currentPage = 1;
        this.loadPlanes();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          if(attr === "capacity") {
            a = a["xcapacity"] + a["ycapacity"];
            b = b["xcapacity"] + b["ycapacity"];
          }
          else {
            a = a[attr];
            b = b[attr];
          }
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatState(row) {
        return row.state === 400 ? "正常" : row.state === 401 ? "检修" : "报废";
      },
      formatCapacity(row) {
        return row.xcapacity + row.ycapacity;
      },
      searchResult () {
        var _this = this;
       this.filterParams.keywords = this.$refs.PlaneSearchBar.keywords;
       this.currentPage = 1;
       this.loadPlanes();
      },
      deletePlane (planeId) {
        this.$confirm('确认删除该班机?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delete/planes', {planeId: planeId}).then(resp => {
              if (resp && resp.data.code === 200) {
                this.$message({message: '删除成功', type: 'success'})
                this.load()
              }
              else{
                this.$message({message: '删除失败: 请检查是否存在相关航线', type: 'error'})
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
        // alert(id)
      },
      editPlane (item) {
        this.$refs.PlaneEditForm.dialogFormVisible = true
        this.$refs.PlaneEditForm.form = {
          planeId: item.planeId,
          planeNum: item.planeNum,
          type: item.type,
          xcapacity: item.xcapacity,
          ycapacity: item.ycapacity,
          state: item.state
        }
      }
    }
  }
</script>

<style scoped>

</style>



