<template>
  <div>
    <i class="el-icon-circle-plus-outline" style="margin: 0; font-size: 45px;  position: relative; top: -65px; left: 250px;" @click="dialogFormVisible = true"></i>
    <el-dialog
      title="添加/修改"
      :modal-append-to-body = "false"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="航班编号" :label-width="formLabelWidth" prop="flightNum" for="flightNum">
          <el-input v-model="form.flightNum" id="flightNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="航线编号" :label-width="formLabelWidth" prop="airlineeId" for="airlineId">
          <el-select v-model="form.airlineId" id="airlineId" filterable placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.airlineNum"
              :label="item.airlineNum"
              :value="item.airlineId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="日期" :label-width="formLabelWidth" prop="flightDate" for="flightDate">
          <el-date-picker
            v-model="form.flightDate"
            align="right"
            type="date"
            placeholder="请选择日期"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth" prop="state" for="flightId">
          <el-radio-group v-model="form.state" size="small">
            <el-radio-button label="300">待飞</el-radio-button>
            <el-radio-button label="301">正在检票</el-radio-button>
            <el-radio-button label="302">正在途中</el-radio-button>
            <el-radio-button label="303">延误</el-radio-button>
            <el-radio-button label="304">取消</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="flightId" style="height: 0" for="flightId">
          <el-input type="hidden" v-model="form.flightId" id="flightId" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'FlightEditForm',
    data () {
      return {
        dialogFormVisible: false,
        form: {
          flightId: '',
          flightNum: '',
          airlineId: '',
          flightDate: '',
          state: ''
        },
        pickerOptions: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周后',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        options: [],
        formLabelWidth: '120px'
      }
    },
    mounted: function () {
      this.loadAirlines()
    },
    methods: {
      clear () {
        this.form = {
          flightId: '',
          flightNum: '',
          airlineId: '',
          flightDate: '',
          state: ''
        }
      },
      loadAirlines() {
        var _this = this;
        this.$axios.get('/airlines').then(resp => {
          if (resp && resp.status === 200) {
            _this.options = resp.data
          }
        });
      },
      onSubmit () {
        var ok = true;
        if(this.form.flightNum ==='' || this.form.airlineId ===''){
          this.$message({
            message: '编号不能为空',
            type: 'warning'
          });
          ok = false;
        }
        if(this.form.state ===''){
          this.$message({
            message: '请选择状态',
            type: 'warning'
          });
          ok = false;
        }
        if(ok){
              this.$axios
                .post('/flights', {
                  flightId: this.form.flightId,
                  flightNum: this.form.flightNum,
                  airline: {airlineId: this.form.airlineId},
                  flightDate: this.form.flightDate,
                  state: this.form.state
                }).then(resp => {
                  if (resp && resp.data.code === 200) {
                    this.dialogFormVisible = false
                    this.$message({message: '添加/修改成功', type: 'success'})
                    this.$emit('onSubmit')
                  }
                  else {
                    this.$message.error('该编号已被使用');
                  }
                }).catch(failResponse => {
                  console.log('error');
                })
        }
      }
    }
  }
</script>

<style scoped>
  .el-icon-circle-plus-outline {
    margin: 50px 0 0 20px;
    font-size: 100px;
    float: left;
    cursor: pointer;
  }
</style>





