<template>
  <div>
    <i class="el-icon-circle-plus-outline" style="margin: 0; font-size: 45px;  position: relative; top: -65px; left: 250px;" @click="dialogFormVisible = true"></i>
    <el-dialog
      title="添加/修改"
      :modal-append-to-body = "false"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="客机编号" :label-width="formLabelWidth" prop="planeNum" for="planeNum">
          <el-input v-model="form.planeNum" id="planeNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="机型" :label-width="formLabelWidth" prop="type" for="type">
          <el-select v-model="form.type" id="type" placeholder="请选择机型">
            <el-option label="A220" value="A220"></el-option>
            <el-option label="A220-100" value="A220-100"></el-option>
            <el-option label="A220-300" value="A220-300"></el-option>
            <el-option label="A300" value="A300"></el-option>
            <el-option label="A300B4" value="A300B4"></el-option>
            <el-option label="A300-600" value="A300-600"></el-option>
            <el-option label="A310" value="A310"></el-option>
            <el-option label="A310-200" value="A310-200"></el-option>
            <el-option label="A310-300" value="A310-300"></el-option>
            <el-option label="A320" value="A320"></el-option>
            <el-option label="A318" value="A318"></el-option>
            <el-option label="A319" value="A319"></el-option>
            <el-option label="A321" value="A321"></el-option>
            <el-option label="A330" value="A330"></el-option>
            <el-option label="A330-200" value="A330-200"></el-option>
            <el-option label="A330-300" value="A330-300"></el-option>
            <el-option label="A330-800" value="A330-800"></el-option>
            <el-option label="A330-900" value="A330-900"></el-option>
            <el-option label="A330neo" value="A330neo"></el-option>
            <el-option label="A340" value="A340"></el-option>
            <el-option label="A340-200" value="A340-200"></el-option>
            <el-option label="A340-300" value="A340-300"></el-option>
            <el-option label="A340-500" value="A340-500"></el-option>
            <el-option label="A340-600" value="A340-600"></el-option>
            <el-option label="A350" value="A350"></el-option>
            <el-option label="A350-900" value="A350-900"></el-option>
            <el-option label="A350-1000" value="A350-1000"></el-option>
            <el-option label="A380" value="A380"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="头等舱座数" :label-width="formLabelWidth" prop="xcapacity" for="xcapacity">
          <el-input v-model="form.xcapacity" id="xcapacity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="经济舱座数" :label-width="formLabelWidth" prop="ycapacity" for="ycapacity">
          <el-input v-model="form.ycapacity" id="ycapacity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth" prop="state" for="planeId">
          <el-radio-group v-model="form.state" size="small">
            <el-radio-button label="400">正常</el-radio-button>
            <el-radio-button label="401">检修</el-radio-button>
            <el-radio-button label="402">报废</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="planeId" style="height: 0" for="planeId">
          <el-input type="hidden" v-model="form.planeId" id="planeId" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'PlaneEditForm',
    data () {
      return {
        dialogFormVisible: false,
        form: {
          planeId: '',
          planeNum: '',
          type: '',
          xcapacity: '',
          ycapacity: '',
          state: ''
        },
        formLabelWidth: '120px'
      }
    },
    methods: {
      clear () {
        this.form = {
          planeId: '',
          planeNum: '',
          type: '',
          xcapacity: '',
          ycapacity: '',
          state: ''
        }
      },
      onSubmit () {
        var ok = true;
        if(this.form.planeNum ===''){
          this.$message({
            message: '编号不能为空',
            type: 'warning'
          });
          ok = false;
        }
        if(this.form.state ===''){
          this.$message({
            message: '请选择状态',
            type: 'warning'
          });
          ok = false;
        }
        if( (this.form.xcapacity !=='' && (this.form.xcapacity < 0 || this.form.xcapacity % 1 !== 0)) ||
        (this.form.ycapacity !=='' && (this.form.ycapacity < 0 || this.form.ycapacity % 1 !== 0)) ){
          this.$message({
            message: '座数必须为正整数',
            type: 'warning'
          });
          ok = false;
        }
        if(ok){
              this.$axios
                .post('/planes', {
                  planeId: this.form.planeId,
                  planeNum: this.form.planeNum,
                  type: this.form.type,
                  xcapacity: this.form.xcapacity,
                  ycapacity: this.form.ycapacity,
                  state: this.form.state,
                }).then(resp => {
                  if (resp && resp.data.code === 200) {
                    this.dialogFormVisible = false
                    this.$message({message: '添加/修改成功', type: 'success'})
                    this.$emit('onSubmit')
                  }
                  else {
                    this.$message.error('该编号已被使用');
                  }
                }).catch(failResponse => {
                  console.log('啊');
                })
        }
      }
    }
  }
</script>

<style scoped>
  .el-icon-circle-plus-outline {
    margin: 50px 0 0 20px;
    font-size: 100px;
    float: left;
    cursor: pointer;
  }
</style>





