<template>
  <div class="container-fluid">
    <el-row>
      <airline-search-bar @onSearch="searchResult" ref="AirlineSearchBar"></airline-search-bar>
      <airline-edit-form @onSubmit="load()" ref="AirlineEditForm"></airline-edit-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        stripe
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="航线编号">
                <span>{{ scope.row.airlineNum }}</span>
              </el-form-item>
              <el-form-item label="执飞班机">
                <span>{{ scope.row.plane.planeNum }}</span>
              </el-form-item>
              <el-form-item label="出发地">
                <span>{{ scope.row.departure }}</span>
              </el-form-item>
              <el-form-item label="到达地">
                <span>{{ scope.row.destination }}</span>
              </el-form-item>
              <el-form-item label="登机时间">
                <span>{{ scope.row.checkTime }}</span>
              </el-form-item>
              <el-form-item label="起飞时间">
                <span>{{ scope.row.takeOffTime }}</span>
              </el-form-item>
              <el-form-item label="到达时间">
                <span>{{ scope.row.arriveTime }}</span>
              </el-form-item>
              <el-form-item label="头等舱票价">
                <span>{{ scope.row.xprice }}</span>
              </el-form-item>
              <el-form-item label="经济舱票价">
                <span>{{ scope.row.yprice }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="airlineNum"
          label="航线编号"
          sortable="custom"
          width="120">
        </el-table-column>
        <el-table-column
          prop="plane.planeNum"
          label="执飞班机"
          width="120">
        </el-table-column>
        <el-table-column
          prop="departure"
          label="出发地"
          width="120">
        </el-table-column>
        <el-table-column
          prop="destination"
          label="到达地"
          width="120">
        </el-table-column>
        <el-table-column
          prop="checkTime"
          label="登机时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="takeOffTime"
          label="起飞时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="arriveTime"
          label="到达时间"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="xprice"
          label="头等舱票价"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          prop="yprice"
          label="经济舱票价"
          sortable="custom"
          width="150">
        </el-table-column>
        <el-table-column
          column-key="active"
          prop="active"
          label="状态"
          width="100"
          :formatter="formatActive"
          :filters="[{ text: '启用', value: 200 }, { text: '停用', value: 201 }]"
          filter-placement="bottom-end">
        </el-table-column>
        <el-table-column
          label="操作"
          width="150">
          <template slot-scope="scope">
            <el-button type="primary" size="small"  @click="edit(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="deleteAirline(scope.row.airlineId)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import AirlineEditForm from './AirlineEditForm.vue'
  import AirlineSearchBar from './AirlineSearchBar.vue'
  export default {
    name: 'AirlineList',
    components: {AirlineEditForm,AirlineSearchBar},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          active: '',
          keywords: ''
        }
      }
    },
    mounted: function () {
      this.load()
    },
    methods: {
      load () {
        var _this = this
        if(this.filterParams.active === '' && this.filterParams.keywords === '') {
          this.$axios.get('/airlines').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          this.$axios.get('/search/airlines',{params:{activeCodes:this.filterParams.active,keywords:this.filterParams.keywords}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      filterChange(filter) {
        this.filterParams.active = filter.active.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          if(attr === "capacity") {
            a = a["xcapacity"] + a["ycapacity"];
            b = b["xcapacity"] + b["ycapacity"];
          }
          else {
            a = a[attr];
            b = b[attr];
          }
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatActive(row) {
        return row.active === 200 ? "启用" : "停用";
      },
      searchResult () {
        var _this = this
       this.filterParams.keywords = this.$refs.AirlineSearchBar.keywords;
       this.currentPage = 1;
       this.load();
      },
      deleteAirline(airlineId) {
        this.$confirm('确认删除该航线?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delete/airlines', {airlineId: airlineId}).then(resp => {
              if (resp && resp.data.code === 200) {
                this.$message({message: '删除成功', type: 'success'})
                this.load()
              }
              else{
                this.$message({message: '删除失败: 请检查是否存在相关航班', type: 'error'})
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
        // alert(id)
      },
      edit (item) {
        this.$refs.AirlineEditForm.dialogFormVisible = true
        this.$refs.AirlineEditForm.form = {
          airlineId: item.airlineId,
          airlineNum: item.airlineNum,
          planeId: item.plane.planeId,
          departure: item.departure,
          destination: item.destination,
          checkTime: item.checkTime,
          takeOffTime: item.takeOffTime,
          arriveTime: item.arriveTime,
          xprice: item.xprice,
          yprice: item.yprice,
          active: item.active
        }
      }
    }
  }
</script>

<style>
  .demo-table-expand {
    margin-left: 80px;
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 35%;
  }
</style>



