<template>
  <div class="container">
    <el-row>
      <flight-search-bar @onSearch="searchResult" ref="FlightSearchBar"></flight-search-bar>
      <flight-edit-form @onSubmit="load()" ref="FlightEditForm"></flight-edit-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        stripe
        style="width: 100%">
        <el-table-column
          prop="flightNum"
          label="航班编号"
          sortable="custom"
          width="200">
        </el-table-column>
        <el-table-column
          prop="airline.airlineNum"
          label="航线编号"
          width="200">
        </el-table-column>
        <el-table-column
          prop="flightDate"
          label="日期"
          sortable="custom"
          width="300">
        </el-table-column>
        <el-table-column
          column-key="state"
          prop="state"
          label="状态"
          width="150"
          :formatter="formatState"
          :filters="[{ text: '待飞', value: 300 }, { text: '正在检票', value: 301 },{ text: '正在途中', value: 302 },{ text: '延误', value: 303 },{ text: '取消', value: 304 }]"
          filter-placement="bottom-end">
        </el-table-column>
        <el-table-column
          label="操作"
          width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="small"  @click="edit(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="deleteFlight(scope.row.flightId)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import FlightEditForm from './FlightEditForm.vue'
  import FlightSearchBar from './FlightSearchBar.vue'
  export default {
    name: 'FlightList',
    components: {FlightEditForm,FlightSearchBar},
    data () {
      return {
        tableData: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          state: '',
          keywords: ''
        }
      }
    },
    mounted: function () {
      this.load()
    },
    methods: {
      load () {
        var _this = this
        if(this.filterParams.state === '' && this.filterParams.keywords === '') {
          this.$axios.get('/flights').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
        else {
          this.$axios.get('/search/flights',{params:{stateCodes:this.filterParams.state,keywords:this.filterParams.keywords}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data
            }
          })
        }
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      filterChange(filter) {
        this.filterParams.state = filter.state.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          a = a[attr];
          b = b[attr];
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatState(row) {
        return row.state === 300 ? "待飞" : row.state === 301 ? "正在检票" : row.state === 302 ? "正在途中" : row.state === 303 ? "延误" : "取消";
      },
      searchResult () {
        var _this = this
       this.filterParams.keywords = this.$refs.FlightSearchBar.keywords;
       this.currentPage = 1;
       this.load();
      },
      deleteFlight(flightId) {
        this.$confirm('确认删除该航班?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delete/flights', {flightId: flightId}).then(resp => {
              if (resp && resp.data.code === 200) {
                this.$message({message: '删除成功', type: 'success'})
                this.load()
              }
              else{
                this.$message({message: '删除失败: 请检查是否存在相关订单', type: 'error'})
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
        // alert(id)
      },
      edit (item) {
        this.$refs.FlightEditForm.dialogFormVisible = true
        this.$refs.FlightEditForm.form = {
          flightId: item.flightId,
          flightNum: item.flightNum,
          airlineId: item.airline.airlineId,
          flightDate: item.flightDate,
          state: item.state
        }
      }
    }
  }
</script>

<style scoped>

</style>



