<template>
  <div>
    <el-dialog
      title="确认购票信息"
      :modal-append-to-body = "false"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="航班号" :label-width="formLabelWidth" prop="flightNum" for="flightNum">
          <el-tag id="flightNum">
            {{form.flightNum}}
          </el-tag>
        </el-form-item>
        <el-form-item label="出发地" :label-width="formLabelWidth" prop="departure" for="departure">
          <p id="departure">{{form.departure}}</p>
        </el-form-item>
        <el-form-item label="目的地" :label-width="formLabelWidth" prop="destination" for="destination">
          <p id="destination">{{form.destination}}</p>
        </el-form-item>
        <el-form-item label="日期" :label-width="formLabelWidth" prop="flightDate" for="flightDate">
          <p id="flightDate">{{form.flightDate}}</p>
        </el-form-item>
        <el-form-item label="登机时间" :label-width="formLabelWidth" prop="checkTime" for="checkTime">
          <p id="checkTime">{{form.checkTime}}</p>
        </el-form-item>
        <el-form-item label="起飞时间" :label-width="formLabelWidth" prop="takeOffTime" for="takeOffTime">
          <p id="takeOffTime">{{form.takeOffTime}}</p>
        </el-form-item>
        <el-form-item label="到达时间" :label-width="formLabelWidth" prop="arriveTime" for="arriveTime">
          <p id="arriveTime">{{form.arriveTime}}</p>
        </el-form-item>
        <el-form-item prop="flightId" style="height: 0" for="flightId">
          <el-input type="hidden" v-model="form.flightId" id="flightId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="头等舱票" :label-width="formLabelWidth" prop="xNum" for="xNum">
          <el-input-number v-model="form.xNum" controls-position="right" @change="handleXChange" :min="0" :max="form.xLimit"></el-input-number>
          <p>{{form.xprice}}元/张</p>
        </el-form-item>
        <el-form-item label="经济舱票" :label-width="formLabelWidth" prop="yNum" for="yNum">
          <el-input-number v-model="form.yNum" controls-position="right" @change="handleYChange" :min="0" :max="form.yLimit"></el-input-number>
          <p>{{form.yprice}}元/张</p>
        </el-form-item>
        <el-form-item label="总价:" :label-width="formLabelWidth" prop="sum" for="sum">
          <el-tag id="sum" type="success">{{form.sum}}元</el-tag>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">提 交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'FlightEditForm',
    data () {
      return {
        dialogFormVisible: false,
        form: {
          flightId: '',
          flightNum: '',
          flightDate: '',
          departure: '',
          destination: '',
          checkTime: '',
          takeOffTime: '',
          arriveTime: '',
          xprice: '',
          yprice: '',
          xLimit: '',
          yLimit: '',
          xNum: 0,
          yNum: 0,
          sum: 0
        },
        add: false,
        orderId: '',
        account: '',
        formLabelWidth: '120px'
      }
    },
    methods: {
      clear () {
        this.form = {
          flightId: '',
          flightNum: '',
          flightDate: '',
          departure: '',
          destination: '',
          checkTime: '',
          takeOffTime: '',
          arriveTime: '',
          xprice: '',
          yprice: '',
          xLimit: '',
          yLimit: '',
          xNum: 0,
          yNum: 0,
          sum: 0
        }
      },
      handleXChange(value){
        this.form.xNum = value;
        this.countPrice();
      },
      handleYChange(value){
        this.form.yNum = value;
        this.countPrice();
      },
      countPrice() {
        this.form.sum = this.form.xprice * this.form.xNum + this.form.yprice * this.form.yNum;
      },
      onSubmit () {
        var ok = true;
        if(this.form.xNum === 0 && this.form.yNum === 0){
          this.$message({
            message: '请选择购票数',
            type: 'warning'
          });
          ok = false;
        }
        if(ok){
          if(this.add){
              this.$axios
                .post('/orders', {
                  orderId: '',
                  account: {},
                  flight: {
                    flightId: this.form.flightId
                  },
                  xnum: this.form.xNum,
                  ynum: this.form.yNum,
                  ifXNear: 0,
                  ifYNear: 0,
                  totalXPrice: this.form.xprice * this.form.xNum,
                  totalYPrice: this.form.yprice * this.form.yNum,
                  state: 500
                }).then(resp => {
                  if (resp && resp.data.code === 200) {
                    this.dialogFormVisible = false
                    this.$message({message: '购票成功', type: 'success'})
                    this.$emit('onSubmit')
                  }
                  else {
                    if(resp.data.code === 400)
                      this.$message.error('购票失败: 不能重复购票');
                    else
                      this.$message.error('购票失败: 晚于起飞前1小时无法购票');
                  }
                }).catch(failResponse => {
                  console.log('error');
                })
          }
          else{
            this.$axios
                  .post('/orders', {
                    orderId: this.orderId,
                    account: this.account,
                    flight: {
                      flightId: this.form.flightId
                    },
                    xnum: this.form.xNum,
                    ynum: this.form.yNum,
                    ifXNear: 0,
                    ifYNear: 0,
                    totalXPrice: this.form.xprice * this.form.xNum,
                    totalYPrice: this.form.yprice * this.form.yNum,
                    state: 500
                  }).then(resp => {
                    if (resp && resp.data.code === 200) {
                      this.dialogFormVisible = false
                      this.$message({message: '修改成功', type: 'success'})
                      this.$emit('onSubmit')
                    }
                    else {
                      this.$message.error('修改失败: 晚于起飞前1小时无法修改');
                    }
                  }).catch(failResponse => {
                    console.log('error');
                  })
          }
        }
      }
    }
  }
</script>

<style scoped>

</style>





