<template>
  <div>
    <i class="el-icon-circle-plus-outline" style="margin: 0; font-size: 45px;  position: relative; top: -65px; left: 250px;" @click="dialogFormVisible = true"></i>
    <el-dialog
      title="添加/修改"
      :modal-append-to-body = "false"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="航线编号" :label-width="formLabelWidth" prop="airlineNum" for="airlineNum">
          <el-input v-model="form.airlineNum" id="airlineNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="执飞班机" :label-width="formLabelWidth" prop="planeId" for="planeId">
          <el-select v-model="form.planeId" id="planeId" filterable placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.planeNum"
              :label="item.planeNum"
              :value="item.planeId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出发地" :label-width="formLabelWidth" prop="departure" for="departure">
          <el-input v-model="form.departure" id="departure" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="到达地" :label-width="formLabelWidth" prop="destination" for="destination">
          <el-input v-model="form.destination" id="destination" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="登机时间" :label-width="formLabelWidth" prop="checkTime" for="checkTime">
          <el-time-picker
            id="checkTime"
            v-model="form.checkTime"
            :editable="true"
            format='HH:mm:ss'
            value-format="HH:mm:ss"
            placeholder="请选择时间">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="起飞时间" :label-width="formLabelWidth" prop="takeOffTime" for="takeOffTime">
          <el-time-picker
            id="takeOffTime"
            v-model="form.takeOffTime"
            :editable="true"
            format='HH:mm:ss'
            value-format="HH:mm:ss"
            placeholder="请选择时间">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="到达时间" :label-width="formLabelWidth" prop="arriveTime" for="arriveTime">
          <el-time-picker
            v-model="form.arriveTime"
            :editable="true"
            format='HH:mm:ss'
            value-format="HH:mm:ss"
            placeholder="请选择时间">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="头等舱票价" :label-width="formLabelWidth" prop="xprice" for="xprice">
          <el-input v-model="form.xprice" id="xprice" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="经济舱票价" :label-width="formLabelWidth" prop="yprice" for="yprice">
          <el-input v-model="form.yprice" id="yprice" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="启用/停用" :label-width="formLabelWidth" prop="active" for="airlineId">
          <el-switch
            style="display: block"
            v-model="form.active"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-value="200"
            inactive-value="201"
            active-text="启用"
            inactive-text="停用">
          </el-switch>
        </el-form-item>
        <el-form-item prop="airlineId" style="height: 0" for="airlineId">
          <el-input type="hidden" v-model="form.airlineId" id="airlineId" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'AirlineEditForm',
    data () {
      return {
        dialogFormVisible: false,
        form: {
          airlineId: '',
          airlineNum: '',
          planeId: '',
          departure: '',
          destination: '',
          checkTime: '',
          takeOffTime: '',
          arriveTime: '',
          xprice: '',
          yprice: '',
          active: 200
        },
        options: [],
        formLabelWidth: '120px'
      }
    },
    mounted: function () {
      this.loadPlanes()
    },
    methods: {
      clear () {
        this.form = {
          airlineId: '',
          airlineNum: '',
          planeId: '',
          departure: '',
          destination: '',
          checkTime: '',
          takeOffTime: '',
          arriveTime: '',
          xprice: '',
          yprice: '',
          active: 200
        }
      },
      loadPlanes() {
        var _this = this;
        this.$axios.get('/planes').then(resp => {
          if (resp && resp.status === 200) {
            _this.options = resp.data
          }
        });
      },
      onSubmit () {
        var ok = true;
        if(this.form.airlineNum ==='' || this.form.planeId ===''){
          this.$message({
            message: '编号不能为空',
            type: 'warning'
          });
          ok = false;
        }
        if(this.form.departure ==='' || this.form.destination ===''){
          this.$message({
            message: '出发/到达地不能为空',
            type: 'warning'
          });
          ok = false;
        }
        if( (this.form.xprice !=='' && this.form.xprice <= 0) ||
        (this.form.yprice !=='' && this.form.yprice <= 0) ){
          this.$message({
            message: '票价必须为正数',
            type: 'warning'
          });
          ok = false;
        }
        if(ok){
              //console.log(this.form.planeId);
              this.$axios
                .post('/airlines', {
                  airlineId: this.form.airlineId,
                  airlineNum: this.form.airlineNum,
                  //planeId: this.form.planeId,
                  plane: {planeId: this.form.planeId},
                  departure: this.form.departure,
                  destination: this.form.destination,
                  checkTime: this.form.checkTime,
                  takeOffTime: this.form.takeOffTime,
                  arriveTime: this.form.arriveTime,
                  xprice: this.form.xprice,
                  yprice: this.form.yprice,
                  active: this.form.active
                }).then(resp => {
                  if (resp && resp.data.code === 200) {
                    this.dialogFormVisible = false
                    this.$message({message: '添加/修改成功', type: 'success'})
                    this.$emit('onSubmit')
                  }
                  else {
                    this.$message.error('该编号已被使用');
                  }
                }).catch(failResponse => {
                  console.log('error');
                })
        }
      }
    }
  }
</script>

<style scoped>
  .el-icon-circle-plus-outline {
    margin: 50px 0 0 20px;
    font-size: 100px;
    float: left;
    cursor: pointer;
  }
</style>





