<template>
  <div class="container">
    <el-row>
      <account-search-bar @onSearch="searchResult" ref="AccountSearchBar"></account-search-bar>
      <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @sort-change="sortChange"
        @filter-change="filterChange"
        stripe
        style="width: 100%">
        <el-table-column
          prop="account.id"
          label="用户ID"
          sortable="custom"
          width="200">
        </el-table-column>
        <el-table-column
          prop="account.username"
          label="用户名"
          sortable="custom"
          width="200">
        </el-table-column>
        <el-table-column
          column-key="rid"
          prop="role.id"
          label="用户类型"
          :formatter="formatRole"
          :filters="roles"
          width="150">
        </el-table-column>
        <!--
        <el-table-column
          label="操作"
          width="200">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="danger" size="small">删除</el-button>
          </template>
        </el-table-column>
        -->
      </el-table>
    </el-row>
    <el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10,20,30,40,50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
  import AccountSearchBar from './AccountSearchBar.vue'
  export default {
    name: 'AccountList',
    components: {AccountSearchBar},
    data() {
      return {
        tableData: [],
        roles: [],
        currentPage: 1,
        pagesize: 10,
        filterParams: {
          role: '',
          keywords: ''
        }
      }
    },
    mounted: function () {
      this.loadRoles()
      this.load();
    },
    methods: {
      load () {
        var _this = this;
        if(this.filterParams.role === '' && this.filterParams.keywords === '') {
          this.$axios.get('/accountRoles').then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data;
            }
          });
        }
        else {
          this.$axios.get('/search/accountRoles',{params:{roleCodes:this.filterParams.role,keywords:this.filterParams.keywords}}).then(resp => {
            if (resp && resp.status === 200) {
              _this.tableData = resp.data;
            }
          });
        }
      },
      loadRoles() {
        var _this = this;
        var items;
        this.roles = [];
        this.$axios.get('/roles').then(resp => {
          if (resp && resp.status === 200) {
            items = resp.data;
            for(var i=0 ; i<items.length ;i++){
              _this.roles.push({text: items[i].name_zh, value: items[i].id});
            }
          }
        });
      },
      handleSizeChange(pageSize) {
        this.currentPage = 1;
        this.pagesize = pageSize;
        console.log('每页' +this.pageSize+'条');
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        console.log('当前页:'+this.currentPage);
      },
      filterChange(filter) {
        this.filterParams.role = filter.rid.join(',');
        this.currentPage = 1;
        this.load();
      },
      sortChange(column) {
        this.currentPage = 1
        this.tableData = this.tableData.sort(this.sortFun(column.prop, column.order === 'ascending'));
      },
      sortFun (attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev = (rev) ? 1 : -1;
        }
        return function (a, b) {
          if(attr === "account.id"){
            a = a.account.id;
            b = b.account.id;
          }
          else if(attr === "account.username"){
            a = a.account.username;
            b = b.account.username;
          }
          else {
            a = a[attr];
            b = b[attr];
          }
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        }
      },
      formatRole(row) {
        return this.roles[row.role.id-1].text;
      },
      searchResult () {
        var _this = this
       this.filterParams.keywords = this.$refs.AccountSearchBar.keywords;
       this.currentPage = 1;
       this.load();
      },
      deleteAccount(id) {
        this.$confirm('确认删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delete/accounts', {id: id}).then(resp => {
              if (resp && resp.data.code === 200) {
                this.$message({message: '删除成功', type: 'success'})
                this.load()
              }
              else{
                this.$message({message: '删除失败: 请检查是否存在相关订单', type: 'error'})
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    }
  }
</script>

<style>
  .demo-table-expand {
    margin-left: 80px;
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



